<template>
    <yd-search v-model="value1" :on-submit="submitHandler"></yd-search>
</template>
<script>
import Vue from "vue";
import { Search } from "vue-ydui/dist/lib.px/search";
Vue.component(Search.name, Search);

export default {
  data() {
    return {
      value1: ""
    };
  },
  methods: {
    submitHandler(value) {
      this.$dialog.toast({ mes: `搜索：${value}` });
    }
  }
};
</script>


